	module.exports = {"data":{"post":{"html":"<p>My folks are flying back to Minnesota from Hamburg this afternoon. That means we'll slowly get back into the swing of things here at home.</p>\n<p>Christopher was apparently quite accustomed to the constant attention from Grandma and Grandpa. When he got home with Mama this evening, he would smile when Mama was next to him, but would start crying as soon as she walked away. Is this the end of our low-maintenance self-entertaining baby?</p>\n<p>For me it was back to work. But now with the Easter and May Day German holidays coming up, we have 4-day work weeks for the next three weeks. I can live with that.</p>","fields":{"title":"Back to the old routine","date":"17 Apr 2000","url":"https://papascott.de/2000-04-17-back-to-the-old-routine/"}}},"pathContext":{"slug":"/2000-04-17-back-to-the-old-routine/","prev":{"fields":{"title":"Congratulations","date":"13 Apr 2000","slug":"/2000-04-13-congratulations/"}},"next":{"fields":{"title":"Waking up","date":"19 Apr 2000","slug":"/2000-04-19-waking-up/"}}}}

